<template>
  <div >
     <!---->
     <el-tabs type="border-card" v-model="activeTabName" >
        <el-tab-pane label="" name="firstTab">
                <div class='title'>
                    <p class='p'><i class="el-icon-s-management"></i>&nbsp;</p>
                    <el-button type="primary" plain @click="addProduct" icon="el-icon-plus" size="mini">
                               </el-button>
                </div>
                 <el-divider></el-divider>
                <div class='content'>
                      <el-form :inline="true" v-model="queryCondition" style="float:left" size="mini">
                        <el-form-item label="">
                          <el-select v-model="queryCondition.productClassfiyId" placeholder="">
                            <el-option v-for="(item,index) in productsSorts" :label="item.productClassfiy" :value="item.id"
                                       @change="productsSortChange" :key="index"></el-option>
                          </el-select>
                        </el-form-item>
                        <el-form-item>
                          <el-button type="primary" @click="queryProductlist"></el-button>
                        </el-form-item>
                      </el-form>

                     <div class="tableStyle">
                        <el-table :data="tableData" style="width: 100%" ref="table"  highlight-current-row
                                   border  height="410" size="mini" :header-row-class-name="getTenHeaderRowName">
                          <el-table-column label="ID"  prop="id" align="center" width="70"></el-table-column>
                          <el-table-column label="" prop="pro_name" align="center" width="250"></el-table-column>
                          <el-table-column label="" prop="productClassfiy" align="center" width="120"></el-table-column>
                          <el-table-column label="" prop="description" align="center"></el-table-column>
                          <el-table-column label="" prop="href" width="200">
                            <template slot-scope="scope">
                              <el-image   style="width:180px; height:80px"  fit="scale-down" lazy
                                          :src="href+scope.row.href" >
                              </el-image>
                            </template>
                          </el-table-column>
                          <el-table-column label="" width="156">
                            <template slot-scope="scope">
                              <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)"></el-button>
                              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
                            </template>
                          </el-table-column>
                        </el-table>
                     </div>

                      <el-pagination layout="total, sizes, prev, pager, next, jumper" :page-sizes="[5, 10, 15, 20]"
                          @size-change="handleSizeChange"
                          @current-change="handleCurrentChange"
                          :current-page="queryCondition.currentPage"
                          :page-size='queryCondition.pageSize'
                          :total='total'>
                        </el-pagination>
                  </div>
        </el-tab-pane>

        <!---->
        <el-tab-pane label="" name="secondTab">
            <productDetailEditor :productDetail="productDetail"></productDetailEditor>
        </el-tab-pane>
      </el-tabs>


  </div>
</template>
<script>
import website from "@/mixins/website";
import productDetailEditor from "./productDetailEditor";
export default {
    name: 'productsManage',
    mixins:[website],
    components: {productDetailEditor},

    mounted () {
          this._queryproductsSorts();
          this.queryProductlist();
    },
    data(){
        return{
            activeTabName:'firstTab',
            total:0,
            editor: null,
            href:this.BASE_URL+'/images/products/',
            productDetail:{
              pro_name:'',
              productClassfiyId:'',
              description:'',
              editorContent:'',
            },
            productsSorts:[],
            queryCondition:{               //
              currentPage:1,
              pageSize:5,
              productClassfiyId:'',
            },
            tableData: [{
              id:'',
              pro_name:'',
              productClassfiy:'',
              description:'',
            },]
        }
    },
    methods:{
      queryProductlist(){
        console.log('',this.queryCondition)
        this.$axios.get(this.BASE_URL+'/admin/products/queryProductlist',{params:this.queryCondition})
        .then((response)=>{
            this.total = response.data.data.total;
            this.tableData = response.data.data.selectList;
            console.log('',response);
        })
        .catch(function (error) {
            console.log(error);
        });
      },
      //-tab
      addProduct(){
        this.activeTabName="secondTab";
        this.productDetail={
          pro_name:'',
          productClassfiyId:'',
          editorContent:'',
          description:'',
        }
      },
      //-
      handleEdit(index,row){
        console.log('',row)
        this.activeTabName="secondTab";
        this.productDetail={
          pro_name:row.pro_name,
          productClassfiyId:row.productClassfiyId,
          description:row.description,
          editorContent:row.content,
        }
        console.log('', this.productDetail)
      },

      //-/
      submitProductDetail(){
        console.log('',this.productDetail);
        this.$axios.post(this.BASE_URL+'/admin/products/addProduct',this .productDetail)
         .then((response)=>{
            console.log('---',response);
            if( response.data.status===0){
               this.$message({
                  message: '',
                  type: 'success'
                });
                //
                this.productDetail={
                    pro_name:'',
                    productClassfiy:'',
                    editorContent:'',
                    Introduction:'',
                  }
                this.queryProductlist();
            }else{
               this.$message({
                showClose: true,
                message: '',
                type: 'error'
              });
            }
        })
        .catch(function (error) {
            console.log(error);
        });

      },
      //
      handleDelete(index,row){
         console.log('row',row)
          this.$axios.get(this.BASE_URL+'/admin/products/deleteProduct/'+row.id)
           .then((response)=>{
            console.log('---',response);
            if( response.data.status===0){
               this.$message({
                  message: '',
                  type: 'success'
                });
                //
                this.productDetail={
                    pro_name:'',
                    productClassfiy:'',
                    editorContent:'',
                    Introduction:'',
                  }
                this.queryProductlist();
            }else{
               this.$message({
                showClose: true,
                message: '',
                type: 'error'
              });
            }
         })
        .catch(function (error) {
            console.log(error);
        });

      },
      cancel(){

      },
      //
      productsSortChange(){
        this.queryCondition.currentPage=1;
        this.queryCondition.pageSize=5;
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
        this.queryCondition.pageSize = val;
        this.queryProductlist();
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
        this.queryCondition.currentPage = val;
        this.queryProductlist();
      },
      getTenHeaderRowName({ row, rowIndex }) {
          return 'tableStyle-header'
      },
      wangEditer(){
          this.editor = new E(this.$refs.editorElem);
          //this.editor.customConfig.uploadImgServer = '/upload';     // 
          this.editor.customConfig.uploadImgShowBase64 = true;        //  base64 
          // html
          this.editor.customConfig.onchange = html => {
          this.productDetail.editorContent = html;
          this.catchData(this.productDetail.editorContent);           // htmlcatchData
          };
          this.editor.customConfig.menus = [
            // 
            'head',          // 
            'bold',          // 
            'fontSize',      // 
            'fontName',      // 
            'italic',        // 
            'underline',     // 
            'strikeThrough', // 
            'foreColor',     // 
            'backColor',     // 
            'link',          // 
            'list',          // 
            'justify',       // 
            'quote',         // 
            'emoticon',      // 
            'image',         // 
            'table',         // 
            'code',          // 
            'undo',          // 
            'redo'           // 
          ];
          this.editor.create(); // 
      },

    },
}
</script>
<style scoped >
 .title{
   background: #FFFFFF;
   width: 100%;
   height:45px;
  }
  .p{
   float:left;
   margin:0px;
   font-size:16px;
   font-weight:bold;
  }
  .content{
   margin:0px;
   padding: 0%;
  }
  .el-popper{
     z-index: 999999;
  }
  .tableStyle .tableStyle-header th{
        border-right: solid 1px #BED0EC;
        background-color: #56A9FF;
        text-align: center;
        color:#FFFFFF;
        box-shadow: 5px 5px 5px 2px rgba(0,0,0,0.1);
    }
  .tableStyle .tableStyle-row td{
        font-family: 'MicrosoftYaHei';
        font-size: 16px;
        border-bottom: solid 1px #BED0EC;
        border-right: solid 1px #BED0EC;
        text-align: center;
        padding: 2px 0;
    }

</style>



