<template>
  <div >
              <div>
                <el-form ref="form" :model="productDetail" label-width="80px">
                    <el-form-item label="" >
                      <el-input v-model="productDetail.pro_name"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                      <el-select v-model="productDetail.productClassfiyId" placeholder="">
                        <el-option v-for="(item,index) in productsSorts" :label="item.productClassfiy" :value="item.id" :key="index"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="">
                      <el-input v-model="productDetail.Introduction"></el-input>
                    </el-form-item>
                  </el-form>
              </div>
              <div id="wangeditor">
                <div ref="editorElem" style="text-align:left;"></div>
              </div>
              <el-button type="primary" plain @click="submitProductDetail"></el-button>
              <el-button type="primary" plain @click="cancel"></el-button>

  </div>
</template>
<script>
import E from "wangeditor";
import website from "@/mixins/website";
export default {
    name: 'productDetailEditor',
    mixins:[website],
    props:{
       productDetail:Object,
    },
    components: {},
    mounted () {
        this._queryproductsSorts();
        this.wangEditer();          //
    },
    watch:{
        productDetail:{
        handler:function(val,oldval){
          console.log('')
          this.wangEditer();
        },
        deep:true                   //
      },

    },
    data(){
        return{
            activeTabName:'firstTab',
            total:0,
            editor: null,
         //   editorContent: '',
            productsSorts:[],
            queryCondition:{
              pro_name:'',
              productSort:'',
              currentPage:1,
              pageSize:5,
            },
            tableData: [{
              id:'',
              pro_name:'',
              productClassfiy:'',
              Introduction:'',
            },]
        }
    },
    methods:{
      //-/
      submitProductDetail(){
        console.log('',this.productDetail);
        this.$axios.post(this.BASE_URL+'/admin/products/addProduct',this.productDetail)
         .then((response)=>{
            console.log('---',response);
            if( response.data.status===0){
               this.$message({
                  message: '',
                  type: 'success'
                });
                //
                this.productDetail={
                    pro_name:'',
                    productClassfiy:'',
                    editorContent:'',
                    Introduction:'',
                  }
                this.queryProductlist(); //
            }else{
               this.$message({
                showClose: true,
                message: '',
                type: 'error'
              });
            }
        })
        .catch(function (error) {
            console.log(error);
        });

      },
      cancel(){

      },
      wangEditer(){
          console.log('2',this.productDetail)

          this.editor = new E(this.$refs.editorElem);
          this.editor.customConfig.zIndex = 100;
          //this.editor.customConfig.uploadImgServer = '/upload';     // 
          this.editor.customConfig.uploadImgShowBase64 = true;        //  base64 
          // html
          this.editor.customConfig.onchange = html => {
          this.productDetail.editorContent = html;
          this.catchData(this.productDetail.editorContent);           // htmlcatchData
          };
          this.editor.customConfig.menus = [
            // 
            'head',          // 
            'bold',          // 
            'fontSize',      // 
            'fontName',      // 
            'italic',        // 
            'underline',     // 
            'strikeThrough', // 
            'foreColor',     // 
            'backColor',     // 
            'link',          // 
            'list',          // 
            'justify',       // 
            'quote',         // 
            'emoticon',      // 
            'image',         // 
            'table',         // 
            'code',          // 
            'undo',          // 
            'redo'           // 
          ];
          this.editor.create(); // 
          console.log('this.productDetail.editorContent',this.productDetail.editorContent)
           this.editor.txt.html(this.productDetail.editorContent);
         // this.editor.txt.html('');
      },

    },
}
</script>
<style scope >
 .title{
   background: #FFFFFF;
   width: 100%;
   height:45px;
  }
  .p{
   float:left;
   margin:0px;
   font-size:16px;
   font-weight:bold;
  }
  .content{
   margin:0px;
   padding: 0%;
  }

  .el-popper{
     z-index: 999999;
  }
  .tableStyle .tableStyle-header th{
        border-right: solid 1px #BED0EC;
        background-color: #56A9FF;
        text-align: center;
        color:#FFFFFF;
        box-shadow: 5px 5px 5px 2px rgba(0,0,0,0.1);
    }
  .tableStyle .tableStyle-row td{
        font-family: 'MicrosoftYaHei';
        font-size: 16px;
        border-bottom: solid 1px #BED0EC;
        border-right: solid 1px #BED0EC;
        text-align: center;
        padding: 2px 0;
    }

</style>



